@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
*,*:before,*:after{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:inherit}html{line-height:1.15;-webkit-text-size-adjust:100%}body{margin:0}main{display:block}h1{font-size:2em;margin:0.67em 0}hr{box-sizing:content-box;height:0;overflow:visible}pre{font-family:'monospace', monospace;font-size:1em}a{background-color:transparent}a img{outline:none;border:none}abbr[title]{border-bottom:none;text-decoration:underline;-webkit-text-decoration:underline dotted;text-decoration:underline dotted}b,strong{font-weight:bolder}code,kbd,samp{font-family:'monospace', monospace;font-size:1em}small{font-size:80%}sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline}sub{bottom:-0.25em}sup{top:-0.5em}img{border-style:none}button,input,optgroup,select,textarea{font-family:inherit;font-size:100%;line-height:1.15;margin:0}button,input{overflow:visible}button,select{text-transform:none}button,[type='button'],[type='reset'],[type='submit']{-webkit-appearance:button}button::-moz-focus-inner,[type='button']::-moz-focus-inner,[type='reset']::-moz-focus-inner,[type='submit']::-moz-focus-inner{border-style:none;padding:0}button:-moz-focusring,[type='button']:-moz-focusring,[type='reset']:-moz-focusring,[type='submit']:-moz-focusring{outline:1px dotted ButtonText}fieldset{padding:0.35em 0.75em 0.625em}legend{box-sizing:border-box;color:inherit;display:table;max-width:100%;padding:0;white-space:normal}progress{vertical-align:baseline}textarea{overflow:auto}[type='checkbox'],[type='radio']{box-sizing:border-box;padding:0}[type='number']::-webkit-inner-spin-button,[type='number']::-webkit-outer-spin-button{height:auto}[type='search']{-webkit-appearance:textfield;outline-offset:-2px}[type='search']::-webkit-search-decoration{-webkit-appearance:none}::-webkit-file-upload-button{-webkit-appearance:button;font:inherit}details{display:block}summary{display:list-item}template{display:none}[hidden]{display:none}figure{margin:0}.c-form{display:flex;max-width:500px;padding:0 20px;margin:0 auto}.c-heading{padding:10px;text-align:center;background-color:#000}@media all and (min-width: 480px){.c-heading{padding:40px}}.c-heading__text{margin:0;font-family:"Poppins",sans-serif;font-size:30px;color:#fff}@media all and (min-width: 480px){.c-heading__text{font-size:40px}}.c-input{width:100%;padding:10px;font-family:"Poppins",sans-serif;border:1px solid #000}.c-item,.c-item--completed{display:flex;align-items:center}.c-item--completed .c-item__circle{background-color:#000}.c-item--completed .c-item__circle::before{display:block}.c-item--completed .c-item__text{color:dimgray;text-decoration:line-through}.c-item__circle{position:relative;width:10px;height:20px;margin-right:20px;background-color:#fff;border:1px solid #000;cursor:pointer}.c-item__circle::before{display:none;position:absolute;top:2px;right:8px;width:5px;height:11px;border-right:2px solid #fff;border-bottom:2px solid #fff;transform:rotate(45deg);content:''}.c-item__cross{position:relative;width:22px;height:22px;padding:0;margin-left:auto;background-color:transparent;border:none;cursor:pointer}.c-item__cross::before,.c-item__cross::after{position:absolute;top:0;left:15px;width:2px;height:22px;background-color:#333;content:''}.c-item__cross::before{transform:rotate(45deg)}.c-item__cross::after{transform:rotate(-45deg)}.c-item__text{font-family:"Poppins",sans-serif;font-size:18px}.c-list{display:grid;grid-row-gap:10px;row-gap:10px;max-width:500px;padding:20px;margin:0 auto}

